import { handleError, handleResponse } from "./apiUtils";
const baseUrl = process.env.API_URL + "/api/courses";

export function getCourses() {
  return fetch(baseUrl)
    .then(handleResponse)
    .catch(handleError);
}

export function saveCourse(course) {
  const url = course._id ? `${baseUrl}/update/${course._id}`:`${baseUrl}/new`;
  return fetch(url, {
    method: course._id ? "PATCH" : "POST", // POST for create, PUT to update when id already exists.
    headers: { "content-type": "application/json" },
    body: JSON.stringify(course)
  })
    .then(handleResponse)
    .catch(handleError);
}

export function deleteCourse(courseId) {
  return fetch(`${baseUrl}/delete/${courseId}`, { method: "DELETE" })
    .then(handleResponse)
    .catch(handleError);
}
